.dcf-calculator {
    font-family: Arial, sans-serif;
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #f4f4f9;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .dcf-calculator h2 {
    text-align: center;
    color: #333;
  }
  
  .dcf-calculator label {
    display: block;
    margin-bottom: 10px;
    color: #555;
  }
  
  .dcf-calculator input {
    width: calc(100% - 20px);
    padding: 10px;
    margin-top: 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .dcf-calculator table {
    width: 100%;
    margin-top: 20px;
    border-collapse: collapse;
  }
  
  .dcf-calculator table th, 
  .dcf-calculator table td {
    padding: 10px;
    text-align: center;
    border: 1px solid #ccc;
  }
  
  .dcf-calculator table th {
    background-color: #f0f0f5;
    color: #333;
  }
  
  .dcf-calculator table td {
    background-color: #fff;
    color: #333;
  }
  
  .dcf-calculator div {
    margin-bottom: 20px;
  }
  